.home {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.home__header {
  display: flex;
  justify-content: space-between;
  padding: 20px 30px;
  align-items: center;
}

.home__header a {
  margin-right: 20px;
  text-decoration: inherit;
  font-size: 15px;
  color: #232528;
}

.home__header a:hover {
  text-decoration: underline;
  color: #000000;

}.home__headerAvatar{
  cursor: pointer;
}

.home__headerRight {
  display: flex;
  align-items: center;
  min-width: 13vw;
  justify-content: space-between;
}

.home__headerRight>.MuiSvgIcon-root {
  margin-right: 20px;
}

.home__body {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-top: 10%;
  max-height: 100vh;
}

.home__logo{
  display: flex;
  margin: 0 auto;
}

.home__logo>img:first-of-type {
  object-fit: contain;
  height: 100px;
  align-items: center;
}

.home__reactLogo {
  animation: rotate-center 3s linear infinite forwards;
  height: 100px;
  width: 100px;

}

@-webkit-keyframes rotate-center {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotate-center {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media (max-width: 480px) {

  .home > *{
    font-size: 12px !important;
  }

  .home__logo{
    flex-direction: column-reverse;
    align-items: center;
  }

  .home__logo > img:first-of-type{
    height: 60px;
  }

  .home__logo > img:last-of-type{
    height: 30px;
  }

  .home__header a {
    font-size: unset !important;
  }
}
