.searchPage__header {
  display: flex;
  position: sticky;
  top: 0;
  background-color: #ffffff;
  z-index: 100;
  align-items: flex-start;
  padding: 30px;
  border-bottom: 1px solid #d3d3d3;
}

.searchPage__logo {
  /* object-fit: contain; */
  height: 30px;
  margin-right: 30px;
}

.searchPage__headerBody .search .search__input {
  margin-top: 0;
  width: unset;
  margin: unset;
  max-width: unset;
}

.searchPage__options {
  display: flex;
  align-items: center;
  color: #808080;
}

.searchPage__optionsLeft,
.searchPage__optionsRight {
  display: flex;
}

.searchPage__optionsLeft a,
.searchPage__optionsRight a {
  text-decoration: none;
  color: #808080;
  margin-left: 2px;
  font-size: 14px;
}

.searchPage__option {
  display: flex;
  align-items: center;
  padding: 0 8px;
}

.searchPage__optionsRight {
  margin-left: 80px;
}

.searchPage__resultCount {
  color: #808080;
  font-weight: 400;
}

.searchPage__results {
  margin: 8px 0 100px 150px;
}

.searchPage__result>a {
  padding: .2em 0;
  text-decoration: none;
}

.searchPage__resultLink {
  color: black;
  font-weight: 400;
}

.searchPage__resultTitle {
  font-size: 1.4rem;
}

.searchPage__resultTitle:hover {
  text-decoration: underline;
}

.searchPage__result {
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  width: 652px;
  color: #808089;
  line-height: 1.1em;
  letter-spacing: .5px;
}

.searchPage__resultSnippet {
  padding: 10px 0;
}

.searchPage__resultImage {
  height: 20px;
  width: 20px;
  margin-right: 10px;
  object-fit: contain;
}

@media (max-width: 480px) {
  .searchPage__header {
    display: block;
    position: relative;
  }

  .searchPage__results {
    margin: 8px;
  }

  .searchPage__result {
    margin-right: 8px;
    padding: 4px;
    width: 100%;
    border-radius: 8px;
    box-shadow: 0px 0px 5px -2px #000000;
  }

  .searchPage__header{
    padding: 5px 5px 10px 05px!important;
    align-items: center;
    display: flex;
    flex-direction: column;
  }

  .searchPage__logo{
    height: 30px;
    margin-top: 5px;
    margin-right: unset;
  }
}